      h2 1.3.	Destinos turísticos: atractivos, geografía, contexto económico, social, cultural y ambiental

    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p Un destino turístico es un sistema complejo que articula recursos naturales y culturales, infraestructura de apoyo, servicios turísticos, gestión institucional y participación comunitaria. Según Buhalis (2020), los destinos son entidades vivas que deben gestionarse de forma integral, buscando un equilibrio entre competitividad y sostenibilidad. Cada destino posee una identidad propia, determinada por su encanto natural, su relevancia histórica, sus tradiciones culturales o la combinación de estos factores. Los atractivos turísticos constituyen el núcleo de todo destino, es por ello que a continuación se explican dichos factores:
    
