    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p Los modelos de sistemas turísticos, en su mayoría, se fundamentan en lo planteado por Beni (2003), quien, a partir de la denominación del término “Modelo de Sistema Turístico (MST)”, explicó cómo funciona la actividad turística desde una visión integral y sostenible. Según Beni, el turismo no puede entenderse como un conjunto de elementos aislados —como hoteles, atractivos o transporte—, sino como un sistema compuesto por subsistemas interdependientes que deben funcionar de manera coordinada para garantizar un desarrollo equilibrado y sostenible del destino.
      p El MST se estructura en los siguientes subsistemas principales, cuya interacción permite comprender el funcionamiento global del destino:

    
    br
    .row.justify-content-center(data-aos="fade-right")
