    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p El modelo sistémico del destino turístico facilita la planificación estratégica, al permitir identificar fortalezas, debilidades, oportunidades y relaciones entre los componentes del sistema, contribuyendo a un desarrollo equilibrado, competitivo y sostenible. 
      p La sostenibilidad constituye el principio integrador del modelo sistémico del destino turístico, al articular los componentes económico, social, cultural y ambiental en un equilibrio que garantice el bienestar de las comunidades locales y la preservación de los recursos. Estas dimensiones orientan la gestión del turismo hacia la equidad, la responsabilidad y la permanencia en el tiempo. Este modelo promueve una visión holística del turismo, donde la preservación del patrimonio vivo, la equidad social y la educación ambiental son pilares esenciales del turismo responsable y tienen una dinámica cíclica, como se aprecia en la siguiente gráfica:

